import React from 'react';
import {AppUI} from './AppUI'

function App() {
  const localStorageTodos = localStorage.getItem('ToDos_v1');
  let parsedTodos;
  if (!localStorageTodos) {
    localStorage.setItem('ToDos_v1', JSON.stringify([]));
    parsedTodos = [];
  }else{
    parsedTodos = JSON.parse(localStorageTodos);
  }
  const [ToDos, setToDos] = React.useState(parsedTodos);
  const [searchValue, setSearchValue] = React.useState('');
  const completedTodos = ToDos.filter(tarea => !!tarea.completed).length;
  const totalToDos = ToDos.length;
  let SearchedTodos = [];
  if (!searchValue.length>=1){
    SearchedTodos = ToDos;
  }else{
    SearchedTodos = ToDos.filter(todo => {
      const todoText = todo.text.toLowerCase();
      const searchText = searchValue.toLowerCase();
      return todoText.includes(searchText);
    })
  }
const saveTodos = (newTodos) => {
  const stringifidedTodos = JSON.stringify(newTodos);
  localStorage.setItem('ToDos_v1', stringifidedTodos);
  setToDos(newTodos);
};
const completeTodo = (text)=>{
  const todoIdex = ToDos.findIndex(todo => todo.text === text);
  const newTodos = [...ToDos];
  newTodos[todoIdex].completed = true;
  saveTodos(newTodos);
}
const deleteTodo = (text)=>{
  const todoIdex = ToDos.findIndex(todo => todo.text === text);
  const newTodos = [...ToDos];
  newTodos.splice(todoIdex, 1);
  saveTodos(newTodos);
}
  return (
    <AppUI
      totalToDos={totalToDos}
      completedTodos={completedTodos}
      searchValue={searchValue}
      setSearchValue={setSearchValue}
      SearchedTodos={SearchedTodos}
      completeTodo={completeTodo}
      deleteTodo={deleteTodo}
    />
  );
}
export default App;
